import { Button } from '@mui/material'
import React, { useEffect, useState } from 'react'
import './login.scss'
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { loginUser } from '../../action/userActions';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const Login = () => {

    const { register, handleSubmit, formState: { errors } } = useForm();
    const navigate = useNavigate();

    const [showPass, setShowPass] = useState(false);

    const toggle = () => {
        setShowPass(!showPass);
    }

    const [data, setData] = useState({
        email: "",
        password: "",
    });

    const handleChange = (e) => {
        setData({ ...data, [e.target.name]: e.target.value });
    }



    /////////////////////////////// authentication ///////////////////////////////
    const dispatch = useDispatch();
    const token = localStorage.getItem("accessToken");
    const { user } = useSelector(state => state.userReducer);

    useEffect(() => {
        if (token && user) {
            navigate('/');
        }
    }, [user, token]);


    ////////////////-------- Toast -//////////////////////////////
    const toast_fun = (error) => {
        toast.error(`Error: ${error}`, { theme: "colored", autoClose: 2000 });
    }


    ///////////////////////////////// redux /////////////////////////////////////////
    const submitData = () => {
        dispatch(loginUser(data, toast_fun));
    }

    return (
        <>

            {/* //////////////////////////////////////////////////// */}
            <ToastContainer />
            {/* //////////////////////////////////////////////////// */}


            <div className='main'>
                <div className='Gatekeeper_login'>
                    <div className='header'>
                        <h1>Billfy</h1>
                        <span>Operator</span>
                    </div>

                    <form onSubmit={handleSubmit(submitData)}>
                        <div className="form-group">
                            <label>User ID</label>
                            <input type="email" name="email" className="form-control" aria-describedby="emailHelp" placeholder="Enter your gatekeeper email" value={data.email}  {...register("email", { onChange: (e) => { handleChange(e) }, required: "userId is required!" })} />
                            <MailOutlineIcon className='icon1' />
                            <div className='error'>{errors.email?.message}</div>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input type={showPass ? "text" : "password"} name="password" className="form-control" placeholder="Enter your password" value={data.password} {...register("password", { onChange: (e) => { handleChange(e) }, required: "password is required!" })} />
                            <LockOpenIcon className='icon1' />

                            <span onClick={toggle}>
                                {showPass ? <VisibilityOffIcon className='icon2' /> :
                                    <VisibilityIcon className='icon2' />}
                            </span>

                            <div className='error'>{errors.password?.message}</div>

                        </div>

                        <div className='btnLogin'>
                            <Button className='bt' type='submit' variant='contained'>Login</Button>
                        </div>

                    </form>

                </div>
            </div>
        </>
    )
}

export default Login